import React,{useState} from 'react'

export default function TextForm(props) {

    const handleUpClick = () => {
      //  console.log("uppercase was clicked" + text);  
        let newtext= text.toUpperCase();
        setText(newtext);
        if(newtext==='')
        {
            props.showAlert("Enter text","alert")
        }
        else{
            props.showAlert("Converted to uppercase ","success")
        }
        
    }
    const handleLoClick = () => {
        //  console.log("uppercase was clicked" + text);  
          let newtext= text.toLowerCase();
          setText(newtext);
          props.showAlert("Converted to lowercase ","success")
      }
      const handleClearClick = () => {
          let newtext= '';
          setText(newtext);
          props.showAlert("text cleared","success")
      }
      const handlespaces = () => {
        let newtext= text.trim();
        setText(newtext);
        props.showAlert("Extra spaces cleared ","success")
    }
    
    const handleCopy = () => {
        let newtext= document.getElementById("myBox");
        newtext.select();
        navigator.clipboard.writeText(newtext.value);
        document.getSelection().removeAllRanges();
        props.showAlert("Text copied","success")
    }
    const handleOnChange = (event) => {
       // console.log("on change");
        setText(event.target.value);
    }

    const [text,setText] = useState('');
    
    return (
        <>
            <div className="container" style ={{color :props.mode==='dark'?'white' : 'black'}}>
                <h1>{props.heading} </h1>
                <textarea className="form-control" value={text} style ={{backgroundColor :props.mode==='dark'?'#13466E' : 'white' , color :props.mode==='dark'?'white' : 'black' }} onChange={handleOnChange} id="myBox" rows="8"></textarea>
                <button disabled={text.length===0}className="btn btn-primary my-3" onClick={handleUpClick}>Convert to Uppercase</button>
                <button disabled={text.length===0}className="btn btn-primary my-3 mx-3" onClick={handleLoClick}>Convert to Lowercase</button>
                <button disabled={text.length===0}className="btn btn-primary my-3 " onClick={handleClearClick}>Clear Text</button>
                <button disabled={text.length===0}className="btn btn-primary my-3 mx-3" onClick={handlespaces}>Trim Whitespaces</button>
                <button disabled={text.length===0}className="btn btn-primary my-1 " onClick={handleCopy}>Copy</button>
            </div>
            <div className="container my-3" style ={{color :props.mode==='dark'?'white' : 'black'}}>
                <h2>Your text summary </h2>
                <p> {text.split(" ").filter((element)=>{return element.length!==0}).length}  words and {text.length} characters</p>
                <p> {0.008*text.split(" ").filter((element)=>{return element.length!==0}).length}  minutes read</p>
                <h2> Preview</h2>
                <p> {text.length>0?text:"Nothing to preview"} </p>
            </div>
      </>
        
    )
}

